@keyframes touch{from{left:0}to{left:100%}}@keyframes arrow{from{width:0}to{width:100%}}.instructions p{margin-bottom:5px}.orientation-container{display:flex;justify-content:center;margin:-20px 0 -10px 0}.orientation-container .inner-container{position:relative}.orientation-container .clients-container{margin:20px -15px;display:flex;flex-wrap:nowrap;align-items:center;justify-content:center}.orientation-container .touch-animation{position:absolute;top:63%;left:0;transform:translate(-50%, -50%);animation-name:touch;animation-duration:5s;animation-iteration-count:infinite}.orientation-container .arrow-animation{position:absolute;top:50%;left:0;transform:translate(0, -50%);animation-name:arrow;animation-duration:5s;animation-iteration-count:infinite;border-bottom:3px solid #546e7a}



